<template>
    <div class="store-search" :class="[{'active':active}]" @touchmove.prevent>
        <!-- 输入区域 -->
        <div class="input-section">
            <!-- 假占位 -->
            <div :style="{'visibility':active?'hidden':'visible'}" class="search-model">
                <span class="iconfont icon-chakan"></span><span class="tips">输入店铺名或编号</span>
            </div>
            <!-- 输入框 -->
            <form :style="{'opacity':active?1:0}" @submit="boardSearchClick" action="#">
                <span class="iconfont icon-chakan"></span>
                <input class="search-text" @textInput="textInutCheck" type="search" ref="input" @focus="focusHandle" v-model="currentValue" placeholder="输入店铺名或编号" >
                <div class="text-clear-div" v-show="showClear" ref="clearBtn" @click="clearTextHandle">
                    <span class="iconfont icon-close"></span>
                </div>
            </form>
        </div>
        <!-- 展示区域 -->
        <div v-show="active" class="show-section">
            <div v-show="showHistory" class="top">
                <span class="top-tip">历史搜索：</span>
                <span class="iconfont icon-lajilou" @click="clearHistory"></span>
            </div>
            <div v-show="!showHistory" class="top">
                <span class="top-tip">{{searchReults&&searchReults.length>0?'搜索结果如下：':'未找到记录~'}}</span>
            </div>
            <div ref="betterscroll" class="better-scroll">
                <div class="scroll-wapper">
                    <!-- 历史记录 -->
                    <div v-show="showHistory" class="history-list">
                        <div class="show-item" v-for="(item,index) in historyData" :key="index" @click="historySearch(item)" @touchstart="onCellDown" @touchcancel="onCellUp" @touchend="onCellUp">
                            <span class="name">{{item}}</span>
                        </div>
                    </div>
                    <!-- 搜索结果 -->
                    <div v-show="!showHistory" class="result-list">
                        <div class="show-item result" v-for="(item,index) in searchReults" :key="index" @click="resultItemClickHandle(item)" @touchstart="onCellDown" @touchcancel="onCellUp" @touchend="onCellUp">
                            <span class="name">{{item.storename}}</span>
                            <div class="right">
                                <span class="NO">{{item.storekey}}</span>
                                <span class="iconfont icon-jiantou-copy"></span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!-- 遮挡 -->
        <transition name="fade">
            <div v-show="active" class="page-cover" @click="disactiveHandle"></div>
        </transition>
    </div>
</template>

<script>
import Vue from "vue";
import { mapState } from "vuex";
import { savaToLocal, loadFromlLocal } from "../../common/js/storage.js";
import BScroll from "better-scroll";

export default {
  name: "store-search",
  props: {},
  data() {
    return {
      currentValue: "", //当前的文本值
      showClear: false, //是否显示清除按钮
      active: false, //是否是激活状态
      showHistory: true,
      historyData: null, //历史搜索
      searchReults: null
    };
  },
  created() {
    this.historyData = loadFromlLocal("aml", "store_search", []);
  },
  watch: {
    currentValue(val) {
      if (val == "") {
        this.showClear = false;
        this.showHistory = true;
        this.refreshBScroll();
      } else this.showClear = true;
    }
  },
  methods: {
    initBetterScroller() {
      if (!this.betterscroll) {
        this.$nextTick(() => {
          this.betterscroll = new BScroll(this.$refs.betterscroll, {
            click: true
          });
        });
      } else {
        this.refreshBScroll();
      }
    },
    refreshBScroll() {
      this.betterscroll.scrollTo(0, 0);
      this.$nextTick(() => {
        this.betterscroll.refresh();
      });
    },
    textInutCheck(event) {
      if (this.currentValue.length >= 13) {
        event.returnValue = false;
      }
      if (!this.currentValue && event.data == " ") {
        event.returnValue = false;
      }
    },
    focusHandle(event) {
      if (this.active) return;
      this.active = true;
      this.initBetterScroller();
    },
    initiativeBlur() {
      this.$refs.input.blur();
      this.currentValue = "";
    },
    boardSearchClick(event) {
      this.trySearch(event);
    },
    trySearch(boardEvent = null) {
      if (boardEvent) boardEvent.preventDefault();
      if (!this.currentValue || this.currentValue == "") {
        return;
      }
      this.$refs.input.blur();
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + "php/Route.php?c=Store", {
          type:'selectStore',
          select:this.currentValue
        })
        .then(response => {
          this.$store.dispatch("toggleLoading", false);
          //添加历史记录
          this.historyData.unshift(this.currentValue);
          this.saveHistory();
          this.searchReults = response.body.arr;
          this.showHistory = false;
          this.refreshBScroll();
        })
        .catch(response => {
          console.log(response);
          this.$store.dispatch("toggleLoading", false);
        });
    },
    clearTextHandle(event) {
      event.stopPropagation();
      this.currentValue = "";
      this.$refs.input.focus();
    },
    disactiveHandle() {
      this.currentValue = "";
      this.active = false;
    },
    clearHistory() {
      //清空历史记录
      console.log("清空历史记录");
      this.historyData = [];
      savaToLocal("aml", "store_search", []);
    },
    filterRepeatHistory() {
      //去掉历史记录里重复的搜索关键字
      this.historyData = this.historyData.filter(function(
        element,
        index,
        self
      ) {
        return self.indexOf(element) == index;
      });
    },
    historySearch(keyword) {
      this.currentValue = keyword;
      this.trySearch();
    },
    saveHistory() {
      //保存历史记录
      this.filterRepeatHistory();
      if (this.historyData.length > 10) this.historyData.pop();
      savaToLocal("aml", "store_search", this.historyData);
    },
    resultItemClickHandle(item) {
      this.disactiveHandle();
      this.$emit("selectResult", item);
    },
    onCellUp(e) {
      let cell = e.currentTarget;
      cell.style.backgroundColor = "transparent";
    },
    onCellDown(e) {
      let cell = e.currentTarget;
      cell.style.backgroundColor = "#e3e3e3";
    }
  },
  components: {},
  computed: {
    ...mapState(["soursePath", "userid", "path"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.store-search {
    font-family: 'Microsoft YaHei';

    &.active {
        height: 100%;

        .input-section {
            background-color: #fff;
        }
    }

    .input-section {
        box-sizing: border-box;
        background-color: #f1f1f1;
        border-radius: 0.04rem;
        height: 0.3rem;
        position: relative;
        z-index: 23;

        .search-model {
            pointer-events: none;
            color: #727272;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);

            .icon-chakan {
                font-size: 0.13rem;
            }

            .tips {
                margin-left: 0.04rem;
                font-size: 0.13rem;
            }
        }

        form {
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;

            .icon-chakan {
                pointer-events: none;
                color: #000;
                position: absolute;
                left: 0.08rem;
                top: 50%;
                transform: translate(0, -50%);
                font-size: 0.14rem;
            }

            .search-text {
                font-size: 0.14rem;
                color: #000;
                flex: 1;
                -webkit-appearance: none; // 去掉iphone背景阴影
                border: none;
                outline: none;
                height: 100%;
                padding-left: 0.28rem;
                background-color: transparent;
            }

            .search-text[type=search]::-webkit-input-placeholder {
                color: #999;
                font-size: 0.13rem;
            }

            .text-clear-div {
                position: relative;
                z-index: 24;
                flex: 0 0 0.3rem;
                text-align: center;

                .icon-close {
                    font-size: 0.18rem;
                    color: #333;
                }
            }

            .search-text[type=search]::-webkit-search-cancel-button {
                -webkit-appearance: none; // 此处只是去掉默认的小×
            }
        }
    }

    .show-section {
        margin-top: 0.02rem;
        position: relative;
        z-index: 23;
        width: 100%;
        height: 1.56rem;
        background-color: rgba(255, 255, 255, 0.95);
        padding-top: 0.04rem;
        border-radius: 0.04rem;

        .top {
            box-sizing: border-box;
            width: 100%;
            padding: 0 0.1rem 0 0.14rem;
            height: 0.23rem;
            display: flex;
            align-items: center;
            justify-content: space-between;

            .top-tip {
                font-size: 0.12rem;
                color: #666;
            }

            .icon-lajilou {
                margin-top: 0.02rem;
                font-size: 0.22rem;
                color: #999;
            }
        }

        .show-item {
            width: 100%;
            height: 0.26rem;
            padding: 0 0.14rem 0 0.14rem;
            box-sizing: border-box;
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: 0.12rem;
            color: #666;

            &.result {
                font-size: 0.13rem;
            }

            .right {
                .NO {
                    margin-right .06rem;
                }

                .iconfont {
                    font-size .12rem;
                    color #aaa;
                }
            }
        }

        .better-scroll {
            height: 1.3rem;
            overflow: hidden;
        }
    }

    .page-cover {
        position: absolute;
        z-index: 21;
        width: 100%;
        left: 0;
        top: 0;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.3);
    }
}
</style>


