<template>
    <div class="bind-store">
        <!-- 搜索 -->
        <store-search ref="storeSearch" @selectResult="selectStoreHandle"></store-search>
        <div class="bottom-box">
            <template v-if="!bindItem">
                <p class="tip">请先搜索相关店铺</p>
            </template>
            <template v-else>
                <p class="tip">已选择：{{bindItem.storename}}&nbsp;&nbsp;<span class="no">编号{{bindItem.storekey}}</span></p>
            </template>
            <div class="btn-box">
              <mt-button size="large" class="bind-btn" type="default" @click.native="bindHandle">立即绑定</mt-button>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState } from "vuex";
import storeSearch from "../public/storeSearch";
import Vue from "vue";
import { Toast, Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
import "mint-ui/lib/toast/style.css";
Vue.component(Button.name, Button);
Vue.component(Toast.name, Toast);

export default {
  name: "bind-store",
  components: {
    storeSearch,
    clearId: -1000
  },
  data() {
    return {
      bindItem: null //绑定的商家信息
    };
  },
  activated() {
    this.bindItem = null;
  },
  beforeRouteLeave(to, from, next) {
    // this.$store.commit("UPDATE_NOBINDSHOW", false);
    if (this.clearId != -1000) {
      clearTimeout(this.clearId);
      this.clearId = -1000;
    }
    next();
  },
  created() {},
  computed: {
    ...mapState(["path", "userid", "bindStoreInfo"])
  },
  beforeRouteLeave(to, from, next) {
    this.$refs.storeSearch.disactiveHandle();
    next();
  },
  methods: {
    bindHandle: function() {
      if (!this.bindItem) {
        Toast({ message: "未选择店铺", duration: 1500 });
        return;
      }
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + "php/Route.php?c=User", {
          type: "userBindBoss",
          userid: this.userid,
          bossid: this.bindItem.bossid
        })
        .then(response => {
          let resultData = response.body;
          this.$store.dispatch("toggleLoading", false);
          if (resultData.error == "ok") {
            this.$store.commit("UPDATA_BIND_STOREINFO", this.bindItem);
            Toast({
              message: "绑定成功",
              iconClass: "iconfont icon-tishi1",
              duration: 1000
            });
            this.clearId = setTimeout(() => {
              this.$router.go(-1);
            }, 800);
          } else {
            Toast({
              message: resultData.msg,
              duration: 1000
            });
          }
        })
        .catch(response => {
          //请求出错处理
          console.log(response);
          this.$store.dispatch("toggleLoading", false);
        });
    },
    selectStoreHandle(item) {
      this.bindItem = item;
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.bind-store {
  padding-top: 0.22rem;
  position: relative;

  .store-search {
    width: 100%;
    box-sizing: border-box;
    padding: 0 0.3rem;
  }

  .bottom-box {
    position: absolute;
    bottom: .16rem;
    left: 0;
    width: 100%;
    padding: 0 0.2rem;
    box-sizing: border-box;
    text-align: center;

    .tip {
      font-size: 0.13rem;
      color: #666;
      line-height: 0.16rem;
      margin-bottom .08rem;

      .no {
        color: #a2733d;
      }
    }

    .btn-box {
      width 90%;
      height .5rem;
      display: inline-flex;
      align-items center;
      justify-content: center;
      border-top: 1px #e3e3e3 dashed;

      .bind-btn {
        height: 0.3rem;
        flex 1;
        background-color: #f8ef94;
        color: #a2733d;
        font-size: 0.13rem;
        letter-spacing: 0.01rem;
        box-shadow: none;
      }
    }
  }
}
</style>





